/* 最大宽度 */
@window-Width: 70%;

.page {
  padding: 32px;
  text-align: center;
}

/* 头部 */
.sidebar-header {
  display: flex;
  justify-content: space-between;
  padding: 16px 32px;
  align-items: center;
  border-bottom: 1px solid #f8f8f8;
}

.sidebar-header .icon {
  color: #2f8cf0;
}

.sidebar-header-title {
  color: #515a6e;
  font-size: 24px;
  font-weight: bold;
  display: flex;
  align-items: center;
}

.sidebar-header-more {
  font-size: 18px;
  color: #989898;
  cursor: pointer;

  &::after {
    content: "";
    display: inline-block;
    width: 12px;
    height: 12px;
    border-top: 1px solid #D9D9D9;
    border-right: 1px solid #D9D9D9;
    transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
  }

  &:hover {
    color: #2d8cf0;
    text-decoration: underline;
  }
}

.v-note-wrapper {
  box-shadow: none !important;
}

.v-note-wrapper .v-note-panel {
  box-shadow: none !important;
}

.v-note-wrapper .v-note-panel .v-note-show .v-show-content, .v-note-wrapper .v-note-panel .v-note-show .v-show-content-html {
  background: #fff !important;
}

.v-note-wrapper .v-note-panel .v-note-edit.divarea-wrapper .content-input-wrapper {
  padding: 0 !important;
}

.v-note-wrapper .v-note-panel .v-note-show .v-show-content, .v-note-wrapper .v-note-panel .v-note-show .v-show-content-html {
  padding: 0 !important;
}





















































.article-empty {
  text-align: center;
  padding: 32px 0;
  color: #515a6e;
  font-size: 16px;
}

.articles-item {
  cursor: pointer;
  padding: 32px;
  display: flex;
  border-bottom: 1px solid #f6f6f6;
  transition: 0.12s ease-in;

  &:hover {
    background: #f6f6f6;
  }

  &:hover .articles-item-title {
    color: #2d8cf0;
  }
}

.articles-item-info {
  flex: 1;
}

.articles-item-title {
  color: #17233d;
  font-size: 26px;
  font-weight: 500;
}

.articles-item-description {
  color: #808695;
  font-size: 18px;
  padding: 24px 0;
}

.articles-item-intro {
  display: flex;
  align-items: center;
}

.articles-item-intro li {
  display: flex;
  align-items: center;
  margin-right: 32px;
  font-size: 16px;
  color: #9ea7b4;
  white-space: nowrap;
}

.articles-item-intro .articles-item-category {
  padding: 5px 32px;
  font-size: 16px;
  color: #409EFF;
  border-radius: 96px;
  background: rgba(51, 119, 255, .1);
}

.articles-images {
  width: 100px;
  margin-left: 32px;

  & img {
    width: 100%;
    border-radius: 6px;
  }
}
