/* 最大宽度 */
.page {
  padding: 32px;
  text-align: center;
}
/* 头部 */
.sidebar-header {
  display: flex;
  justify-content: space-between;
  padding: 16px 32px;
  align-items: center;
  border-bottom: 1px solid #f8f8f8;
}
.sidebar-header .icon {
  color: #2f8cf0;
}
.sidebar-header-title {
  color: #515a6e;
  font-size: 24px;
  font-weight: bold;
  display: flex;
  align-items: center;
}
.sidebar-header-more {
  font-size: 18px;
  color: #989898;
  cursor: pointer;
}
.sidebar-header-more::after {
  content: "";
  display: inline-block;
  width: 12px;
  height: 12px;
  border-top: 1px solid #D9D9D9;
  border-right: 1px solid #D9D9D9;
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
}
.sidebar-header-more:hover {
  color: #2d8cf0;
  text-decoration: underline;
}
.v-note-wrapper {
  box-shadow: none !important;
}
.v-note-wrapper .v-note-panel {
  box-shadow: none !important;
}
.v-note-wrapper .v-note-panel .v-note-show .v-show-content,
.v-note-wrapper .v-note-panel .v-note-show .v-show-content-html {
  background: #fff !important;
}
.v-note-wrapper .v-note-panel .v-note-edit.divarea-wrapper .content-input-wrapper {
  padding: 0 !important;
}
.v-note-wrapper .v-note-panel .v-note-show .v-show-content,
.v-note-wrapper .v-note-panel .v-note-show .v-show-content-html {
  padding: 0 !important;
}
.category {
  width: 100%;
  overflow: hidden;
  overflow-x: auto;
  height: 64px;
  line-height: 64px;
  display: flex;
  border-bottom: 1px solid #f0f0f0;
}
.category-item {
  margin-left: 32px;
  cursor: pointer;
  color: #515a6e;
  font-size: 16px;
  font-weight: normal;
}
.category-item:hover {
  color: #2d8cf0;
}
.category-item-active {
  color: #2d8cf0;
}
.articles {
  width: 70%;
  display: flex;
  min-height: 80vh;
  margin: 24px auto;
}
.article-list {
  flex: 1;
  margin-right: 32px;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 1px 2px 3px #f0f0f0;
  background: #fff;
}
@media screen and (min-width: 200px) and (max-width: 750px) {
  .articles {
    width: 100%;
  }
  .article-list {
    margin-right: 0;
  }
}
